import { createRouter, createWebHashHistory } from 'vue-router'
import index from '../views'
import home from '../views/pages/home.vue'
import login from '../views/pages/login.vue'
import newsList from '@/views/pages/news/list'
import yonghuList from '@/views/pages/yonghu/list'
import yonghuDetail from '@/views/pages/yonghu/formModel'
import yonghuAdd from '@/views/pages/yonghu/formAdd'
import yonghuRegister from '@/views/pages/yonghu/register'
import yonghuCenter from '@/views/pages/yonghu/center'
import jiajuleixingList from '@/views/pages/jiajuleixing/list'
import jiajuleixingDetail from '@/views/pages/jiajuleixing/formModel'
import jiajuleixingAdd from '@/views/pages/jiajuleixing/formAdd'
import jiajushangchengList from '@/views/pages/jiajushangcheng/list'
import jiajushangchengDetail from '@/views/pages/jiajushangcheng/formModel'
import jiajushangchengAdd from '@/views/pages/jiajushangcheng/formAdd'
import storeupList from '@/views/pages/storeup/list'
import addressList from '@/views/pages/shop_address/list'
import cartList from '@/views/pages/shop_order/cart'
import order_confirm from '@/views/pages/shop_order/confirm'
import ordersList from '@/views/pages/shop_order/list'

const routes = [{
		path: '/',
		redirect: '/index/home'
	},
	{
		path: '/index',
		component: index,
		children: [{
			path: 'home',
			component: home
		}
		, {
			path: 'newsList',
			component: newsList
		}
		, {
			path: 'yonghuList',
			component: yonghuList
		}, {
			path: 'yonghuDetail',
			component: yonghuDetail
		}, {
			path: 'yonghuAdd',
			component: yonghuAdd
		}
		, {
			path: 'yonghuCenter',
			component: yonghuCenter
		}
		, {
			path: 'jiajuleixingList',
			component: jiajuleixingList
		}, {
			path: 'jiajuleixingDetail',
			component: jiajuleixingDetail
		}, {
			path: 'jiajuleixingAdd',
			component: jiajuleixingAdd
		}
		, {
			path: 'jiajushangchengList',
			component: jiajushangchengList
		}, {
			path: 'jiajushangchengDetail',
			component: jiajushangchengDetail
		}, {
			path: 'jiajushangchengAdd',
			component: jiajushangchengAdd
		}
		, {
			path: 'storeupList',
			component: storeupList
		}
		, {
			path: 'addressList',
			component: addressList
		}
		, {
			path: 'cartList',
			component: cartList
		}
		, {
			path: 'order_confirm',
			component: order_confirm
		}
		, {
			path: 'ordersList',
			component: ordersList
		}
		]
	},
	{
		path: '/login',
		component: login
	}
	,{
		path: '/yonghuRegister',
		component: yonghuRegister
	}
]

const router = createRouter({
  history: createWebHashHistory(process.env.BASE_URL),
  routes
})

export default router
